<template>
  <div class="logout">
    <h1>Sign Out</h1>
  </div>
</template>

<script>
export default {
  name: "Logout",

  data() {
    return {};
  },

  created() {
    this.logout();
  },

  methods: {
    logout() {
      var self = this;

      var on_ok = resp => {
        self.jumpToStarting();
      };

      var on_error = resp => {
        self.jumpToStarting();
      };

      this.$store.dispatch("CurrentSession/logout", { on_ok, on_error });
    },

    jumpToStarting() {
      this.$router.push("/starting");
    }
  }
};
</script>
